@use 'variables' as *;
@use 'mixins.scss' as *;

.top-box {
  // padding: 6rem 0 8rem;
  // display: flex;
  align-items: center;

  @include min-960 {
    min-height: 100vh;
    // padding: 10rem 0;
  }
}

.link_split {
  position: relative;
  outline: 0;
  line-height: 1;
  transition: color 0s 0.5s;
  display: inline-block;
  white-space: nowrap;

  &:hover {
    text-shadow: none;
    color: transparent;
    transition: none;

    &::before {
      transform: translate3d(0px, -2px, 0);
    }

    &::after {
      transform: translate3d(0px, 2px, 0);
    }

    &::before,
    &::after {
      color: $text-color;
      transition:
        color 0.5s,
        transform 0.5s;
    }
  }

  &::before,
  &::after {
    content: attr(data-letters);
    position: absolute;
    top: 0;
    left: 0;
    color: #fff;
    transition:
      color 0.5s,
      transform 0.5s;
  }

  &::before {
    clip-path: polygon(0 0, 100% 0%, 100% 30%, 0 65%);
  }

  &::after {
    clip-path: polygon(0 65%, 100% 30%, 100% 100%, 0 100%);
  }
}

.link_underline-center {
  position: relative;
  transition: color 0.3s ease-in-out;

  &::before {
    content: '';
    transform: scaleX(0);
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 1px;
    transition: transform 0.3s ease-in-out;
    opacity: 0.6;
    background-color: $text-color;
  }

  &::after {
    bottom: 0;
    left: 0;
  }

  &:hover {
    &::before {
      transform: scaleX(1);
    }

    a {
      opacity: 1;
    }
  }
}

.link_underline {
  &::before,
  &::after {
    position: absolute;
    width: 0;
    height: 1px;
    content: '';
    transition: width 0.4s ease-in-out;
    opacity: 0.6;
    background-color: $text-color;
  }

  &::before {
    right: 0;
    bottom: 0;
  }

  &::after {
    bottom: 0;
    left: 0;
  }

  &._active {
    &::before {
      width: 45%;
    }

    &::after {
      width: 45%;
    }
  }

  &:hover {
    transition: color 0.4s ease-in-out;
    color: $text-color;

    &::before {
      width: 45%;
    }

    &::after {
      width: 45%;
    }

    a {
      opacity: 1;
    }
  }
}

.is-open {
  display: grid;
  width: 100%;
  transition: grid-template-rows 0.4s ease-in-out;
  grid-template-columns: 1fr;
  grid-template-rows: 0fr;

  &._is-open {
    grid-template-rows: 1fr;
  }
}

//* ------------------------ Трансформация блока--------------------------------
.transform-perspective {
  // -- Cвойства родителя
  transform-style: preserve-3d;
  backface-visibility: hidden;
  perspective: 100px;

  // -- Свойства дочернего элемента
  .transform-box {
    transition: all 0.8s cubic-bezier(0.77, 0, 0.175, 1);

    &:hover {
      opacity: 0.3 !important;
      transform-origin: 50% -100%;
      transform: rotateX(-16deg) translateZ(-22px);
    }
  }
}
//* ----------------------------------------------------------------------------
.grad_underline {
  /* Определяем фоновые изображения */
  transition: background-size 400ms;

  /* Размер фона для каждого слоя */
  background-image:
    linear-gradient(to left, transparent, transparent),
    linear-gradient(
      to left,
      var(--background-selected),
      var(--font-color-active)
    );

  /* Позиционирование фона */
  background-repeat: no-repeat;

  /* Повторение фона */
  background-position:
    100% 100%,
    /* Первое изображение */ 0 100%; /* Второе изображение */

  /* Переход для изменения размера фона */
  background-size:
    100% 1px,
    /* Первое изображение */ 0 1px; /* Второе изображение */
}
//* ----------------------------------------------------------------------------
.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-top: 6rem;

  .swiper-pagination-bullet {
    width: 15px;
    height: 15px;
    content: '';
    border-radius: 50%;
    background: #2a3514;
    transition: background 0.5s ease-in-out;

    &:not(:last-child) {
      margin-right: 2rem;
    }
  }

  .swiper-pagination-bullet-active {
    width: 15px;
    height: 15px;
    content: '';
    border-radius: 50%;
    background: #9acd32;
    scale: 1.2;
    transition: scale 0.5s ease-in-out;
  }

  &.swiper-pagination-lock {
    display: none;
  }
}
