@use '../core/variables' as *;
@use '../core/mixins' as *;

.order-place {
	// .order-place__body
	&__body {
		padding: 6rem 2rem;
		max-width: 86.8rem;
		margin: 0 auto;
		border-radius: 4px;
		background: $background-color;
		box-shadow: 3px 3px 10px 0 rgb(36, 18, 33);
	}

	// .order-place__title
	&__title {
		text-align: center;
		filter: contrast(150%);
	}

	// .order-place__form
	&__form {
		margin-top: 4rem;
	}
}

.order-form {
	// .order-form__line
	&__line {
		display: flex;
		gap: 2rem;

		&:not(:last-child) {
			margin-bottom: 3rem;
		}

		@include screen-490 {
			flex-direction: column;
		}
	}

	// .order-form__col-line
	&__col-line {
		position: relative;
		flex: 0 1 50%;
	}

	// .order-form__input
	&__input {
		border-radius: 4px;
		border: 1px solid $color-Rouse;

		input {
			padding: 10px;
			width: 100%;
			height: 100%;
			filter: contrast(150%);
			font-weight: 500;
			transition: box-shadow 0.3s ease-in-out;

			&:focus {
				box-shadow: 0 0 5px 2px var(--alarm-color);
			}

			&:not(:placeholder-shown) + label,
			&:focus + label {
				transform: translateY(-83%);
			}
		}
	}

	// .order-form__label
	&__label {
		display: inline-flex;
		align-items: center;
		position: absolute;
		top: 0;
		left: 0;
		width: auto;
		height: auto;
		font-weight: 500;
		transition: transform 0.3s ease-in-out;
		background-color: $background-color;
		margin: 10px;
	}

	// .order-form__select-type
	&__select-type {
		position: relative;
	}

	.button {
		width: 100%;
	}
}
